.row{
    position: relative;
}
.content{
    position: absolute;
    top: 150px;
    left: 50%;
    transform: translate(-50%);
}
.container{
    width: 1290px;
}
.articleBox{
    background-color: #fff;
    padding: 35px 75px 61px 75px;
    border: 1px solid #EAEAEA;
    box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.05);
    border-radius: 5px;
    margin-bottom: 35px;
}
.title{
    margin-bottom: 39px;
    font-size: 24px;
    font-weight: 400;
    text-align: center;
}
.description{

}

.button{
    color: #363636;
    font-weight: 700;
    p{
        margin-left: 75px;
        margin-bottom: 45px;
    }
    span{
        margin-right: 6px;
    }
}