.row {
  margin-bottom: 43px;
}

.title {
  font-weight: 600;
  margin-bottom: 10px;
}

.search {
  position: relative;
  margin-bottom: 40px;
}
.search img {
  position: absolute;
  left: 20px;
  top: 15px;
}

.inputText {
  width: 600px;
  padding: 15px 20px 15px 50px;
  font-size: 16px;
  color: #363636;
  border: 1px solid #EAEAEA;
  box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.05);
  border-radius: 5px;
}

.results {
  display: flex;
  flex-direction: column;
  gap: 5px;
  font-weight: 600;
}
.results hr {
  background-color: #EAEAEA;
  border: 0.5px solid #EAEAEA;
}/*# sourceMappingURL=Search.module.css.map */